.image-editor{
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(-30deg, #4404f5, #d498f4);
    display: flex;
    justify-content: center;
    align-items: center;
    .card{
       padding: 20px;
       max-width: 100%;
       width: 700px; 
       max-height: 100%;
       background-color:  #230D21;
        border-radius: 50px;
       box-shadow: 40px 40px 40px rgb(32, 13, 35, 0.5);
       .card_header{
        text-align: center;
        padding-bottom: 15px;
            h2{
                text-transform: uppercase;
                color: #ffffff;
            }
       }
       .card_body{
        display: flex;
        justify-content: center;
            .sidebar{
                .reset{
                    margin-top: 20px;
                    display: flex;
                    flex-direction: column;
                    .save{
                        background-color: #e335f3 !important;
                        
                    }
                    button{
                        &:not(:last-child) {
                            margin-bottom: 10px;
                        }
                        background-color: rgb(242, 53, 85);
                        padding: 8px;
                        border-radius: 3px;
                        outline: none;
                        border: 1px solid #dad7d7;
                        cursor: pointer;
                        color: #fff;
                        font-weight: 600;
                        font-size: 15px;
                        white-space: nowrap;
                        outline: none;

                    }
                }
                span{
                    font-size: 15px;
                    color: #ffffff;
                    margin-bottom: 4px;
                    display: block;
                }
                .side_body{
                    padding: 5px 0px 5px 0px;
                    max-width: 250px;
                    border: 1px solid #c3c5c4;
                    margin-right: 8px;
                    min-height: 300px;
                    padding: 10px;
                    .filter_key{
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        grid-gap: 8px;
                        button.active{
                            background-color: rgb(84,93,216);
                            color: #fff;
                        }
                        button{
                            padding: 8px;
                            background-repeat: 3px;
                            border: 1px solid #dad7d7; 
                            cursor: pointer;
                            color: #404040;
                            text-transform: capitalize;
                        }
                    }
                    .filter_slider{
                        margin: 15px 0px;
                        .label_bar{
                            margin-bottom: 4px;
                            display: flex;
                            justify-content: space-between;
                            label,span{
                                color: #ffffff;
                                font-size: 15px;
                                text-transform: capitalize;
                                
                            }
                        }
                        input{
                            width: 100%;
                        }
                    }
                    .rotate{
                        label{
                            margin-bottom: 8px;
                            display: block;
                            font-size: 15px;
                            color: #ffffff;
                        }
                        .icon{
                            display: grid;
                            grid-template-columns: repeat(4, 1fr);
                            grid-gap: 5px;
                            color: #ffffff;
                            div{
                                font-size: 20px;
                                border: 1px solid rgb(179, 194, 194);
                                padding: 5px 5px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                cursor: pointer; 
                            }
                        }
                    }
                }
            }
            .image_section{
                margin-left: 8px;
                background-color: #8d8d8d;
                height: 350px;
                width: 400px;
                label{
                    color: #ffffff;
                }
                .image{
                    
                    width: 100%;
                    height: 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    img{
                        object-fit: scale-down;
                        transition: all .5s;
                        height: 100%;
                        height: 350px;
                        width: 260px;
                    }
                    label{
                        width: 100%;
                        height: 100%;
                        display: flex;
                        border: dotted;
                        cursor: pointer;
                        justify-content: center;
                        align-items: center;
                        flex-direction: column;
                        border-color: #c5c9c5;
                        img{
                            font-size: 40px;
                            color: #b4caca;
                        }
                        span{
                            color: #404040;
                        }
                    }
                }
                .image_select{
                    margin: 20px 0px;
                    display: flex;
                    justify-content: flex-end;
                    input{
                        display: none;
                    }
                    label, button{
                        padding: 8px;
                        border-radius: 3px;
                        outline: none;
                        border: 1px solid #dad7d7;
                        cursor: pointer;
                        color: #fff;
                        font-weight: 600;
                        font-size: 15px;
                        white-space: nowrap;
                    }
                    .redo, .undo{
                        background-color: #4a55f3;
                        margin-left: 5px;
                        color: #fff;
                        padding: 5px 10px;
                        font-size: 18px;
                    }
                    label{
                        background-color: #581115;
                        margin-left: 5px;
                        text-align: center;
                    }
                    .crop{
                        background-color: #8e33b7;
                        margin-left: 5px;

                    }
                }
            }
       }
    }
}
